
body{
    background-color: #d4d6d7;
}

#NavBar{
    text-align:center;
    overflow: auto;
}

#NavBar-ul {
    list-style-type: none;
    margin-top: 30px;
    margin-bottom: 30px;
    padding: 0;
    overflow: auto; 
    display: inline-block;
}

.NavBar-li {
    float: left;
    background-color: #62666a;
}

.NavBar-li a {
    display: block;
    color: white;
    text-align: center;
    width: 300px;
    text-decoration: none;
    height: 25px;
}

#NavBar li a:hover {
    background-color: #5e768f;
}
 
.NavBarActive {
    background-color: #5e768f;
}

.CompanyInfo{
    background-color: #c0c3c5;
    margin-top: 20px;
    margin-bottom: 0;
    text-align: center;
    overflow: hidden;
    display: flex;            
    display: -webkit-flex;            
    justify-content: center;            
    align-items: center;
    position: relative;
}

.CompanyInfoContent{
    width: 1016px;
    overflow: hidden;
    text-align: middle;
}

#MJX_Title{
    display:inline-block;
    vertical-align: text-bottom;
    float: right;
}

.MJX_TitleText{
    margin: 20px 250px;
}

#MJX_Img{
    vertical-align: text-bottom;
}

#SiteFoot{
    
}

#SiteFoot ul{
    list-style-type: none;
    width: 200px;
    display: inline-block;
    vertical-align: top;
    margin-right:50px;
    margin-left: 0;
    float: left;
}

#SiteFoot li{
    text-align: left;
    border-bottom: medium solid white;
}

#SiteFoot li a{
    display: block;
    color: #000;
    padding: 8px 16px;
    text-decoration: none;
    font-size: 15px;
}

#SiteFoot li a:hover{
    color: #FF7F50;
}

#PageContent{
    overflow: auto;
    display: flex;            
    display: -webkit-flex;
    justify-content: center;            
    align-items: center;
}

.PageContent{
    width: 1016px; 
}

.NavBar-li:hover .NavBar-DropDown-Content{
    height: 100%;
}

.NavBar-DropDown-Content{
    height: 0px;
    overflow: hidden;
    transition: .5s;
    background-color: transparent;
    padding: 0;
    position: absolute;
}

.NavBar-DropDown-Content li{
    text-align: center;
    list-style-type: none;
    background-color: #62666a;
}

#NavBar-ul:after{
  content: ''; 
  display: block; 
  clear: both; 
}
